import uniLoadMore from '../../../components/uni-load-more.vue';
export default {
	 components: {//2注册组件
		uniLoadMore
	},
	data() {
		return {
			keys:'',
			page:1,
			contList:[],
			allPages:'',
			cony:'',
			loadingText: '加载中...',
			loadingType: 0,//定义加载方式 0---contentdown  1---contentrefresh 2---contentnomore
			contentText: {
				contentdown:'上拉显示更多',
				contentrefresh: '正在加载...',
				contentnomore: '没有更多数据了'
			}
		}
	},
	onReachBottom:function(){
		let _this=this;
		_this.articleHelp();
	},
	mounted:function(){
		var _this = this;
		_this.articleHelp();
	},
	methods: {
		//搜索
		articleHelp:function(){
			let _this=this;
			if(_this.cony!=_this.keys){
				_this.cony = _this.keys
				_this.page = 1;
				_this.contList = [];
			}
			let data={
				keys:_this.keys,
				page:_this.page,
			};
			_this.loadingType = 1;
			uni.showNavigationBarLoading()
			_this.$axios(_this.$baseUrl.articleHelp,data).then(res =>{
				if(res.data.status==1){
					if (res.data.result.search == null ||res.data.result.search==undefined ||res.data.result.search=='' ) {//没有数据
					    _this.loadingType = 2;
					    uni.hideNavigationBarLoading();//关闭加载动画
					    return;
					}
					_this.page++
					_this.contList = _this.contList.concat(res.data.result.search)
					_this.allPages = res.data.result.pages;
				}
			}).catch(error =>{
				
			})
		},
	}
}
